import { Component } from '@angular/core';
import { IonicPage, NavParams, ViewController } from 'ionic-angular';
import { Question } from '../../app/app.models';
import { Observable } from 'rxjs';
import { IExamReducer, SetIndex } from '../../reducers/exam.reducer';
import { Store } from '@ngrx/store';
import { AppState } from '../../app/app.state';

@IonicPage()
@Component({
  selector: 'page-avance-examen',
  templateUrl: 'avance-examen.html',
})
export class AvanceExamenPage {

  public qList: Question[] | Question[][]
  public isGroup: boolean = false

  public completed: string[] = []
  public activeIndex: number = 0

  private examState$: Observable<IExamReducer>

  constructor(
    private store: Store<AppState>,
    private navParams: NavParams,
    public viewCtrl: ViewController,
  ) {
    this.examState$ = this.store.select('exam')
  }

  ngOnInit() {

    // Load questions
    const questions: Question[] | Question[][] = this.navParams.get('questions');
    if (questions[0] instanceof Array) this.isGroup = true
    console.log(questions, this.isGroup)
    this.qList = questions

    this.examState$.subscribe(state => {
      if (state.results && state.results.questions) this.completed = Object.keys(state.results.questions)
      if (state.finished) this.completed = []
      this.activeIndex = state.index
    })
  }

  gotoQuestion(index: number) {
    this.store.dispatch(new SetIndex(index))
    this.viewCtrl.dismiss();
  }

}



